.banner {
    height: 80vh; 
    color: white;
    background-size: cover; 
    background-position: center;
    background-repeat: no-repeat;
    position: relative; 
}

.banner_contents {
    margin-left: 30px;
    padding-top: 20vh; 
    position: absolute;
    bottom: 20px; 
    z-index: 10;
    max-width: 50%; 
}

.banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_description {
    width: 100%; 
    line-height: 1.5;
    padding: 1rem 0;
    font-size: 1rem;
    max-width: 360px;
}

.banner_button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 1rem 3rem;
    margin-right: 2rem;
    background-color: rgba(51, 51, 51, 0.5);
}

.play {
    color: #000;
    background-color: #fff;
}

.banner_button:hover {
    color: #000;
    background-color: #c6c6c6;
    transition: all 0.2s;
}

.banner_fadeBottom {
    position: absolute;
    bottom: 0; 
    width: 100%; 
    height: 20vh; 
    background-image: linear-gradient(
        180deg,
        transparent,
        rgb(37, 37, 37, 0.91),
        #111
    );
}
